@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: rgb(56, 56, 56);
  font-family: "Roboto", sans-serif;
}

.addTodoForm {
  padding: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  max-width: 500px ;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

.addTodoFormTitle {
  -webkit-align-self: center;
          align-self: center;
}

.titleInputWrapper {
  position: relative;
  width: 100%;
}

.titleInput {
  width: 100%;
  height: 30px;
  border: transparent;
  border-bottom: 1px solid;
  outline: none;
}

.titleInput::-webkit-input-placeholder {
  color: rgb(180, 180, 180);
}

.titleInput:-ms-input-placeholder {
  color: rgb(180, 180, 180);
}

.titleInput::placeholder {
  color: rgb(180, 180, 180);
}

.titleInput:focus::-webkit-input-placeholder {
  color: rgb(0, 0, 0);
}

.selectUserWrapper {
  position: relative;
  width: 100%;
}

.selectUser {
  width: 100%;
  height: 30px;
}

.todoAddBtn {
  width: 100px;
  height: 40px;
  background-color: rgb(66, 66, 66);
  color: white;
  cursor: pointer;
  border: none;
}

.todoAddBtn:hover {
  background-color: rgb(0, 0, 0);
}

.todoAddBtn:active {
  background-color: rgb(66, 66, 66);
}

.error {
  position: absolute;
  top: -15px;
  font-size: 12px;
  color: rgb(255, 72, 72);
}

.userName {
  font-weight: 700;
  margin-bottom: 10px;
}

.userNamePrefix {
  font-weight: 300;
}

.todoTitle {
  font-size: 20px;
  margin-bottom: 10px;
  overflow-x: hidden;
}

.todoTitleWrapper {
  display: -webkit-flex;
  display: flex;
}

.todoStatus {
  font-weight: 300;
}

.todoList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
  padding-top: 10px;
}

.userCard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgb(255, 255, 255);
  padding: 10px;
  border-radius: 5px;
  max-width: 500px;
}

.taskId {
  font-size: 12px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.todosListTitle {
  color: rgb(255, 255, 255);
  font-weight: 700;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.todosListWrapper {
  padding-top: 10px;
}

